* {
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
	width: 100%;
	height: 100%;
}

body {
	margin: 0;
	min-height: 100vh;
	line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-size: cover;
	background-color: $bg-clr;
	color: $text-clr;
	font-family: $font-family;
	font-size: $font-size-base;
}

.main-app {
	min-height: 100vh;
	min-height: 100dvh;
	overflow-x: hidden;
}

img {
	max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
	font-weight: normal;
	padding: 0;
	display: flex;
	align-items: center;
}
h1 {
	font-size: 2rem;
}

h2 {
	font-size: 1.75rem;
}

h3 {
	font-size: 1.5rem;
}

h4 {
	font-size: 1.25rem;
}

h5 {
	font-size: 1rem;
}

h6 {
	font-size: 0.75rem;
}

p {
	margin-bottom: 1em;
}

a {
	text-decoration: none;
	width: max-content;
	height: max-content;
	color: inherit;
}

button {
	background: inherit;
	color: inherit;
	font-family: inherit;
	padding: 0.6em;
	border: none;
	border-radius: 0.4em;
	background-color: $primary-color;
	cursor: pointer;
	box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
	* {
		pointer-events: none;
	}
	&:hover {
		background-color: $secondary-color;
		box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	}
}

input {
	font-family: inherit;
	text-align: inherit;
	color: inherit;
	background-color: inherit;
	border: 1px solid $border-color;
	border-radius: em(5px);
}

.active:not(:has(.logo, .full-logo)) {
	color: $accent-color;
	box-shadow: 0 0.1rem 0 0 $accent-color;
}

// MAC like scrollbar
::-webkit-scrollbar {
	width: 8px;
	background-color: rgba(0, 0, 0, 0);
	-webkit-border-radius: 100px;
}

::-webkit-scrollbar:hover {
	background-color: rgba(0, 0, 0, 0.09);
}

::-webkit-scrollbar-thumb:vertical {
	background: rgba(0, 0, 0, 0.5);
	-webkit-border-radius: 100px;
}

::-webkit-scrollbar-thumb:vertical:active {
	-webkit-border-radius: 100px;
}
